.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 20rem;
  overflow: auto;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
  
}

.actions {
  text-align: right;
}

.actions button {
  font: inherit;
  cursor: pointer;
  background-color: white;
  color : rgb(88,10,70,1);
  border: 1px solid black;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
}

.actions button:hover,
.actions button:active {
  color: white;
  background-color: rgb(88,10,70,1);
}


.actions .button {
  background-color: white;
  color: rgb(88,10,70,1);
}

.rewards{
  /* height: 40px; */
  color: gold;
}